import React, { useState, useEffect, useRef } from "react";
import { BsCloudUpload } from "react-icons/bs";
import axios from "axios";
import { useTranslation } from "react-i18next";
import i18n from "../i18n";
import MediaRenderer from "../components/MediaRenderer";
export const UploadprivatealbumForGallery = ({
  isOpen,
  onClose,
  children,
  closepopup11,
}) => {
  const handleOverlayClick = (e) => {
    // Check if the clicked element is the overlay
    if (e.target.classList.contains("popup-overlay")) {
      onClose(); // Close the popup if the overlay is clicked
    }
  };
  const [spinner, setspinner] = useState(false);
  const [images, setImages] = useState(null);
  const [visibility, setVisibility] = useState("");
  const [addToAlbum, setAddToAlbum] = useState("No");
  const [addToAlbumPass, setAddToAlbumpass] = useState("No");
  const [Password, setPassword] = useState("");
  const [albumName, setAlbumName] = useState("");
  const [rightsConfirmed, setRightsConfirmed] = useState("Yes");
  const [error, setError] = useState("");
  const [successmsg, setsuccessmsg] = useState("");
  const [preebtn, setpreebtn] = useState(false);
  const { t } = useTranslation();
  const fileInputRef = useRef(null);

  useEffect(() => {
    if (visibility === "Private_visible") {
      setAddToAlbumpass("Yes");
      setAddToAlbum("Yes");
    } else {
      setAddToAlbumpass("No");
    }
  }, [visibility]);

  useEffect(() => {
    const savedLanguage = localStorage.getItem("language") || "en"; // Default to 'en' if no language is saved
    // Change the language if it's not already the current language
    if (i18n.language !== savedLanguage) {
      i18n.changeLanguage(savedLanguage);
    }
  }, [i18n, t]);
  var apiUrl = "https://backend.amourette.no/api/members/";
  const handleFileChange = async (e) => {
    const selectedFiles = e.target.files;
    const maxFileSize = 10 * 1024 * 1024; // 10 MB in bytes
    const maxFileCount = 10; // Maximum of 10 files
    let validFiles = [];
    let fileError = "";

    const supportedImageTypes = ["image/jpeg", "image/png", "image/gif"];

    if (selectedFiles.length > maxFileCount) {
      fileError = `You can only upload a maximum of ${maxFileCount} files.`;
    } else {
      for (const file of Array.from(selectedFiles)) {
        const fileType = file.type;

        if (!fileType.startsWith("image/") && !fileType.startsWith("video/")) {
          fileError = `File "${file.name}" is not an image or video.`;
          break;
        } else if (file.size > maxFileSize) {
          fileError = `File "${file.name}" exceeds the 10 MB limit.`;
          break;
        } else if (
          fileType.startsWith("image/") &&
          !supportedImageTypes.includes(fileType)
        ) {
          // Convert unsupported image to PNG
          try {
            const converted = await convertToPng(file);
            validFiles.push(converted);
          } catch (err) {
            fileError = `Failed to convert image "${file.name}" to PNG.`;
            break;
          }
        } else {
          validFiles.push(file);
        }
      }
    }

    if (fileError) {
      setError(fileError);
    } else {
      setError("");
      setImages(validFiles);
    }
  };
  const convertToPng = (file) => {
    return new Promise((resolve, reject) => {
      const reader = new FileReader();

      reader.onload = function (event) {
        const img = new Image();
        img.onload = function () {
          const canvas = document.createElement("canvas");
          canvas.width = img.width;
          canvas.height = img.height;

          const ctx = canvas.getContext("2d");
          ctx.drawImage(img, 0, 0);

          canvas.toBlob(
            (blob) => {
              if (blob) {
                const pngFile = new File(
                  [blob],
                  file.name.replace(/\.\w+$/, ".png"),
                  {
                    type: "image/png",
                    lastModified: new Date().getTime(),
                  }
                );
                resolve(pngFile);
              } else {
                reject(new Error("Failed to convert image."));
              }
            },
            "image/png",
            1
          );
        };
        img.onerror = reject;
        img.src = event.target.result;
      };

      reader.onerror = reject;
      reader.readAsDataURL(file);
    });
  };

  const [yesNoerror, setYesNoError] = useState(false);

  const handleSubmit = async (e) => {
    e.preventDefault();
    if (!images) {
      alert("Please select images to upload.");
      return;
    }

    const storedUsername = localStorage.getItem("userLogin");
    const userLogin = storedUsername ? JSON.parse(storedUsername) : null;
    const formData = new FormData();

    images.forEach((file) => {
      formData.append("images", file); // Use the key 'files' for your backend to handle multiple files
    });
    formData.append("visibility", visibility);
    formData.append("addToAlbum", "Yes");
    formData.append("albumName", albumName);
    formData.append("password", Password);
    formData.append("rightsConfirmed", rightsConfirmed);
    formData.append("user_id", userLogin.id);
    formData.append("fileType", "");
    if (rightsConfirmed === "No") {
      setYesNoError(true);
      return;
    } else {
      setYesNoError(false);
    }
    setspinner(true);
    setpreebtn(true);
    try {
      const res = await axios.post(
        apiUrl + "saveprivateAlbumGallery",
        formData,
        {
          headers: {
            Accept: "application/json",
            "Content-Type": "multipart/form-data",
          },
        }
      );
      var respo = res.data;
      setpreebtn(false);
      setsuccessmsg(respo.message);
      setTimeout(() => {
        window.location.reload();
        setspinner(false);
        setsuccessmsg("");
        closepopup11();
        setVisibility("");
        setAlbumName("");
        setPassword("");
        setAddToAlbum("No");
        setRightsConfirmed("Yes");
        setImages(null);
      }, 3500);
    } catch (err) {
      // Enhanced error handling
      if (err.response) {
      } else if (err.request) {
        console.error("Request data:", err.request);
      } else {
        console.error("Error message:", err.message);
      }
    }
  };
  const removeFile = (index) => {
    setImages((prev) => {
      const updatedImages = prev.filter((_, i) => i !== index);
      // If no files left, reset the input value
      if (updatedImages.length === 0 && fileInputRef.current) {
        fileInputRef.current.value = "";
      }
      return updatedImages;
    });
  };

  const handlecheck = () => {};
  if (!isOpen) return null;
  return (
    <>
      <div className="popup-overlay" onClick={handleOverlayClick}>
        <div className="popup-content col-md-5">
          <button className="close-button" onClick={onClose}>
            &times; {/* Close button */}
          </button>
          <div className="gallery_pop  d-flex flex-column gap-4">
            <h3> {t("upload_album")}</h3>
            <form
              action="javascript:void(0)"
              method="post"
              onSubmit={handleSubmit}
            >
              <div className="row gy-3 align-items-end">
                <div className="col-md-12">
                  <div className="uploadfile rel bgload">
                    <p className="pb-2">{t("select_one_more")}</p>
                    <input
                      type="file"
                      name="file"
                      required
                      accept="image/*,video/mp4,video/webm,video/ogg"
                      multiple
                      onChange={handleFileChange}
                      className="fileload"
                      ref={fileInputRef}
                    />
                    {error && <p style={{ color: "red" }}>{error}</p>}
                    <div className="inload" style={{ height: "50px" }}>
                      <div className="d-flex gap-3 align-items-center">
                        {images !== null && (
                          <>
                            {images.map((file, index) => (
                              <div className="upimage rel" key={index}>
                                <button
                                  type="button"
                                  onClick={() => removeFile(index)}
                                >
                                  X
                                </button>
                                <div className="inimag ofit">
                                  {file.type.startsWith("image/") ? (
                                    <img
                                      src={URL.createObjectURL(file)}
                                      alt={file.name}
                                    />
                                  ) : (
                                    <video width="160" height="120" controls>
                                      <source
                                        src={URL.createObjectURL(file)}
                                        type={file.type}
                                      />
                                    </video>
                                  )}
                                </div>
                              </div>
                            ))}
                          </>
                        )}

                        <div className="d-flex align-items-center gap-2">
                          <BsCloudUpload style={{ fontSize: "24px" }} />
                          <h3 className="m-0" style={{ fontSize: "14px" }}>
                            {t("upload_image")}/{t("Videos")}
                          </h3>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>

                <div className="col-12">
                  <div className="d-flex flex-column gap-2">
                    <p>{t("how_private")}</p>
                    <div className="d-flex align-items-center gap-2">
                      <div className="icon">
                        <svg
                          xmlns="http://www.w3.org/2000/svg"
                          width="24"
                          height="24"
                          viewBox="0 0 24 24"
                          fill="none"
                          stroke="currentColor"
                          stroke-width="2"
                          stroke-linecap="round"
                          stroke-linejoin="round"
                          class="icon icon-tabler icons-tabler-outline icon-tabler-lock"
                        >
                          <path stroke="none" d="M0 0h24v24H0z" fill="none" />
                          <path d="M5 13a2 2 0 0 1 2 -2h10a2 2 0 0 1 2 2v6a2 2 0 0 1 -2 2h-10a2 2 0 0 1 -2 -2v-6z" />
                          <path d="M11 16a1 1 0 1 0 2 0a1 1 0 0 0 -2 0" />
                          <path d="M8 11v-4a4 4 0 1 1 8 0v4" />
                        </svg>
                      </div>
                      <select
                        value={visibility}
                        onChange={(e) => setVisibility(e.target.value)}
                        className=""
                        required
                        name="visibility"
                      >
                        <option value="">--{t("Select")}--</option>
                        <option value="Public_visible">{t("All")}</option>
                        <option value="Friends_visible">
                          {t("Friends_visible")}
                        </option>
                        {/* <option value="Private_visible">
                          {t("Private_visible")}
                        </option> */}
                      </select>
                    </div>
                  </div>
                </div>
                {addToAlbumPass === "Yes" && (
                  <div className="col-12">
                    <div className="d-flex flex-column gap-2">
                      <p>{t("Password")}</p>
                      <div className="d-flex align-items-center gap-2">
                        <div className="icon">
                          <svg
                            xmlns="http://www.w3.org/2000/svg"
                            width="24"
                            height="24"
                            viewBox="0 0 24 24"
                            fill="none"
                            stroke="currentColor"
                            stroke-width="2"
                            stroke-linecap="round"
                            stroke-linejoin="round"
                            class="icon icon-tabler icons-tabler-outline icon-tabler-lock"
                          >
                            <path stroke="none" d="M0 0h24v24H0z" fill="none" />
                            <path d="M5 13a2 2 0 0 1 2 -2h10a2 2 0 0 1 2 2v6a2 2 0 0 1 -2 2h-10a2 2 0 0 1 -2 -2v-6z" />
                            <path d="M11 16a1 1 0 1 0 2 0a1 1 0 0 0 -2 0" />
                            <path d="M8 11v-4a4 4 0 1 1 8 0v4" />
                          </svg>
                        </div>
                        <input
                          type="password"
                          name="password"
                          placeholder={t("Enterprotectedpasswordhere")}
                          value={Password}
                          onChange={(e) => setPassword(e.target.value)}
                        />
                      </div>
                    </div>
                  </div>
                )}

                <div className="col-12">
                  <div className="d-flex flex-column gap-2">
                    <p>{t("Add_the_album")}</p>
                    <div className="d-flex align-items-center gap-2">
                      <div className="icon">
                        <svg
                          xmlns="http://www.w3.org/2000/svg"
                          width="24"
                          height="24"
                          viewBox="0 0 24 24"
                          fill="none"
                          stroke="currentColor"
                          strokeWidth="2"
                          strokeLinecap="round"
                          strokeLinejoin="round"
                          className="icon icon-tabler icons-tabler-outline icon-tabler-library-photo"
                        >
                          <path stroke="none" d="M0 0h24v24H0z" fill="none" />
                          <path d="M7 3m0 2.667a2.667 2.667 0 0 1 2.667 -2.667h8.666a2.667 2.667 0 0 1 2.667 2.667v8.666a2.667 2.667 0 0 1 -2.667 2.667h-8.666a2.667 2.667 0 0 1 -2.667 -2.667z" />
                          <path d="M4.012 7.26a2.005 2.005 0 0 0 -1.012 1.737v10c0 1.1 .9 2 2 2h10c.75 0 1.158 -.385 1.5 -1" />
                          <path d="M17 7h.01" />
                          <path d="M7 13l3.644 -3.644a1.21 1.21 0 0 1 1.712 0l3.644 3.644" />
                          <path d="M15 12l1.644 -1.644a1.21 1.21 0 0 1 1.712 0l2.644 2.644" />
                        </svg>
                      </div>
                      <input
                        type="text"
                        name="album_name"
                        placeholder={t("Enteralbumname")}
                        value={albumName}
                        onChange={(e) => setAlbumName(e.target.value)}
                        required // Required only if "Yes" is selected
                      />
                    </div>
                  </div>
                </div>

                <div className="col-12">
                  {yesNoerror && (
                    <span style={{ color: "red", fontSize: "12px" }}>
                      {t("Pleaseconfirm")}
                    </span>
                  )}
                  <div className="d-flex flex-column gap-2">
                    <p>{t("Do_you_have_the")}</p>
                    <div className="d-flex align-items-center gap-2">
                      <div className="icon">
                        <svg
                          xmlns="http://www.w3.org/2000/svg"
                          width="24"
                          height="24"
                          viewBox="0 0 24 24"
                          fill="none"
                          stroke="currentColor"
                          stroke-width="2"
                          stroke-linecap="round"
                          stroke-linejoin="round"
                          class="icon icon-tabler icons-tabler-outline icon-tabler-copyright"
                        >
                          <path stroke="none" d="M0 0h24v24H0z" fill="none" />
                          <path d="M12 12m-9 0a9 9 0 1 0 18 0a9 9 0 1 0 -18 0" />
                          <path d="M14 9.75a3.016 3.016 0 0 0 -4.163 .173a2.993 2.993 0 0 0 0 4.154a3.016 3.016 0 0 0 4.163 .173" />
                        </svg>
                      </div>

                      <select
                        value={rightsConfirmed}
                        onChange={(e) => setRightsConfirmed(e.target.value)}
                        name="rightsConfirmed"
                      >
                        <option value="Yes">{t("Yes")}</option>
                        <option value="No">{t("No")}</option>
                      </select>
                    </div>
                  </div>
                </div>
                <span className="text-success">{successmsg}</span>
                <div className="col-12">
                  {spinner && (
                    <div
                      className="spinner-border text-success send_load"
                      role="status"
                    >
                      <span className="visually-hidden">{t("loading")}...</span>
                    </div>
                  )}
                  <button
                    type="submit"
                    style={{
                      pointerEvents: preebtn ? "none" : "auto", // Disable button when preebtn is true
                      opacity: preebtn ? 0.1 : 1, // Reduce opacity when disabled
                    }}
                  >
                    {t("upload")}
                  </button>
                </div>
              </div>
            </form>
          </div>
        </div>
      </div>
    </>
  );
};
